*,
html,
body {
  font-family: "Plus Jakarta Sans", sans-serif;
  box-sizing: border-box;
  overflow-x: hidden;
}

:root {
  font-size: 16px;
}

::placeholder {
  color: rgba(238, 245, 255, 0.4);
  font-size: 1.125rem;
  font-style: normal;
  font-weight: 500;
  line-height: 168.5%;
}

::-ms-input-placeholder {
  color: rgba(238, 245, 255, 0.4);
  font-size: 1.125rem;
  font-style: normal;
  font-weight: 500;
  line-height: 168.5%;
}

.flex-center-center {
  display: flex;
  align-items: center;
  justify-content: center;
}

.flex-center-start {
  display: flex;
  align-items: center;
  justify-content: flex-start;
}

.flex-start-center {
  display: flex;
  align-items: flex-start;
  justify-content: center;
}

.input-control {
  padding: 0px 1.5rem;
  border-radius: 0.5rem;
  border: 1px solid rgba(255, 255, 255, 0.4);
  background: rgba(255, 255, 255, 0.05);
  width: 100%;
  height: 100%;
}

.link {
  text-align: center;
  text-decoration-line: underline;
  text-transform: capitalize;
  background-clip: text !important;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.linear-gard-orange-bg, .common-cta.primary, .link {
  background: var(--accent-gr, linear-gradient(92deg, #FD7702 4.52%, #FC8F01 99.48%));
}

.info-txt {
  background: linear-gradient(145deg, #FFD337 0%, #FF9737 94.46%);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-size: 0.875rem;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
  letter-spacing: 0.1 0.75rem;
}

.error-txt {
  color: #FF96B8;
}

input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus,
input:-webkit-autofill:active {
  background-color: transparent !important;
  color: #fff !important;
}

/* For Firefox */
input:-moz-autofill {
  background-color: transparent !important;
  color: #fff !important;
}

/* Generic for other browsers */
input:-internal-autofill-selected {
  background-color: transparent !important;
  color: #fff !important;
}

.text-16-500, .error-txt {
  font-size: 1rem;
  font-style: normal;
  font-weight: 500;
  line-height: 1.5rem;
}

.text-18-500, .common-cta {
  font-size: 1.125rem;
  font-style: normal;
  font-weight: 500;
  line-height: 168.5%;
}

.text-48-500 {
  font-size: 3rem;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
}

.text-18-normal {
  font-size: 1.125rem;
  font-style: normal;
  font-weight: 400;
  line-height: 1.75rem;
}

.text-16-500, .error-txt {
  font-size: 1rem;
  font-style: normal;
  font-weight: 500;
  line-height: 1.75rem;
}

.text-24-500 {
  font-size: 1.5rem;
  font-style: normal;
  font-weight: 500;
  line-height: 1.75rem;
}

.common-cta {
  padding: 12px 24px;
  width: 100%;
}
.common-cta .icon-arrow-right {
  position: relative;
  top: 1px;
}
.common-cta.primary {
  color: #fff;
}
.common-cta:disabled {
  opacity: 0.5;
}
